import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { CountriesService } from 'app/shared/services/countries.service';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { GovtSubsidyService } from 'app/shared/services/ev-space/govt-subsidy/govt-subsidy.service';
import { MasterService } from 'app/shared/services/master/master.service';
import { EVFleetService } from 'app/shared/services/ev-space/ev-fleet.service';
import { ChargerTypeService } from 'app/shared/services/charger-type/charger-type.service';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}


@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})


export class ViewDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  filteredOptionsVehicle: Observable<any[]>;
  vehicles: any;
  @ViewChild("toggleElement") ref: any;
  status = false;
  localStorage: any;
  isPublish: boolean = false;
  isModrate: boolean = false;
  brands: any;
  filteredOptionsBrand: Observable<any[]>;
  connectorTypes: any;
  filteredConnecterType: Observable<any[]>;
  chargersType: any;
  filteredChargerType: Observable<any[]>;
  menuArray: any;

  constructor(private fb: FormBuilder, private govtSubsidyService: GovtSubsidyService, private snackBar: MatSnackBar, private masterService: MasterService,
    public dialogRef: MatDialogRef<ViewDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: MtxDialogData,
    private chargerTypeService: ChargerTypeService, private evFleetService: EVFleetService,) { }
  ngOnInit() {
    this.initialize();
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
    this.params = this.data;
    this.isEditMode = this.params.action == 'CREATE' ? false : true;
    this.isPublish = this.params.buttonAction == 'publishAction' ? true : false;
    this.isModrate = this.params.buttonAction == 'modrateAction' ? true : false;

    this.form = this.fb.group({
      brand: ['', [autocompleteObjectValidator(), Validators.required]],
      vehicle: ['', [autocompleteObjectValidator(), Validators.required]],
      connector_type: ['', [autocompleteObjectValidator(), Validators.required]],
      charger_type: ['', [Validators.required,]],
      name: [''],
      driver_range: [''],
      image: ['',],
      status: this.status == true ? 'M' : 'N'
    });

    this.getVehicle();
    this.getBrands();
    this.getConnectorTypes();
    this.getChargerTypes();

    if (this.isEditMode) {
      this.assignValues(this.params);
    }

  }

  getVehicle() {
    this.govtSubsidyService.getVehicleTypes().subscribe(
      (resp: any) => {
        this.vehicles = resp;

        if (this.isEditMode) {

          const toSelect = this.vehicles.find(c => c.id == this.params.data.vehicle_type_id);
          this.form.get('vehicle').setValue(toSelect);
        }
        this.filteredOptionsVehicle = this.form.controls.vehicle.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterVehicleModels(name) : this.vehicles)
          );
      }, (err) => {

      }
    )

  }
  displayVehicle(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterVehicleModels(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.vehicles.filter(option => option.name.toLowerCase().includes(filterValue));
  }

  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }
  getBrands() {
    this.masterService.getBrands().subscribe(
      (data: any) => {
        this.brands = data;
        if (this.isEditMode) {
          const toSelect = this.brands.find(c => c.id == this.params.data.brand_id);
          this.form.get('brand').setValue(toSelect);
        }
        this.filteredOptionsBrand = this.form.controls.brand.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterBrands(name) : this.brands)
          );
      },
      (err) => {
      })
  }
  private _filterBrands(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.brands.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayBrand(item: any): string {
    return item && item.name ? item.name : '';
  }

  getConnectorTypes() {
    this.masterService.getConnectorTypes().subscribe(
      (resp: any) => {
        this.connectorTypes = resp;
        if (this.isEditMode) {
          const toSelect = this.connectorTypes.find(c => c.id == this.params.data.connector_type_id);
          this.form.get('connector_type').setValue(toSelect);
        }
        this.filteredConnecterType = this.form.controls.connector_type.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterConnectorType(name) : this.connectorTypes)
          );
      }, (err) => {
      }
    )
  }
  displayConnectorType(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterConnectorType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.connectorTypes.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  getChargerTypes() {
    this.chargerTypeService.getChargerTypes().subscribe(
      (resp: any) => {
        if (resp != null) {
          this.chargersType = resp;
          if (this.isEditMode) {
            const toSelect = this.chargersType.find(c => c.id == this.params.data.charger_type_id);
            this.form.get('charger_type').setValue(toSelect);
          }
          this.filteredChargerType = this.form.controls.charger_type.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterChargerType(name) : this.chargersType)
            );
        }
      }, (err) => {
      }
    )
  }
  displayChargerType(item: any): string {
    return item && item.name ? item.name : '';
  }
  private _filterChargerType(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.chargersType.filter(option => option.name.toLowerCase().includes(filterValue));
  }


  assignValues(params) {
    this.form.get('name').setValue(params.data.name);
    this.form.get('driver_range').setValue(params.data.driver_range);
    this.form.get('image').setValue(params.data.image_url);
    this.status = params.data.status == 'M' ? true : false;
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }

  onPublish() {
    const params: any = {
      id: this.params.data.id,
      status: "Y",
      modify_by: this.localStorage.userId
    }
    this.evFleetService.publishEvFleet(params).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
          this.dialogRef.close();
        }

      },
      (err) => {
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        this.dialogRef.close();
      }
    )
  }
  onModrate() {
    const params: any = {
      id: this.params.data.id,
      status: "M",
      modify_by: this.localStorage.userId
    }
    this.evFleetService.moderateEvFleet(params).subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.snackBar.open(MessageConstant.recordUpdateMessage, 'Dismiss', { duration: 5000 });
          this.dialogRef.close();
        }

      },
      (err) => {
        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
        this.dialogRef.close();
      }
    )
  }
}





