import { Routes } from '@angular/router';
import { ListComponent } from '../list/list.component';
import { CreateComponent } from '../create/create.component';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
import { List2WheelersComponent } from '../list2Wheeler/list2Wheelers.component';
import { List4WheelersComponent } from '../list4Wheelers/list4Wheelers.component';
import { List3WheelersComponent } from '../list3Wheeler/list3Wheelers.component';
import { ElectricBusManufacturesComponent } from '../electric-bus-manufactures/electric-bus-manufactures.component';
import { ElectricTruckManufacturesComponent } from '../electric-truck-manufactures/electric-truck-manufactures.component';
import { ElectricCycleManufacturesComponent } from '../electric-cycle-manufactures/electric-cycle-manufactures.component';
export const TABS_DEMO_ROUTES: Routes = [
  { 
    path: 'create', canActivate: [AuthGuard], component: CreateComponent 
  },
  { 
    path: 'listModerate', canActivate: [AuthGuard], component: ListComponent
  },
  { 
    path: 'list2WheelersManufactures', canActivate: [AuthGuard], component: List2WheelersComponent
  },
  { 
    path: 'list4WheelersManufactures', canActivate: [AuthGuard], component: List4WheelersComponent
  },
  { 
    path: 'list3WheelersManufactures', canActivate: [AuthGuard], component: List3WheelersComponent
  },
  { 
    path: 'listElectricBusManufactures', canActivate: [AuthGuard], component: ElectricBusManufacturesComponent
  },
  { 
    path: 'listElectricTruckManufactures', canActivate: [AuthGuard], component: ElectricTruckManufacturesComponent
  },
  { 
    path: 'listEvCycleManufactures', canActivate: [AuthGuard], component: ElectricCycleManufacturesComponent
  },
  { 
    path: '', redirectTo: 'listModerate', pathMatch: 'full' 
  },
];
