
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActionDialogComponent } from '../action-dialog/action-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { GovtSubsidyService } from 'app/shared/services/ev-space/govt-subsidy/govt-subsidy.service';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { MatTableFilter } from 'mat-table-filter';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { ViewDialogComponent } from '../view-dialog/view-dialog.component';
import * as XLSX from 'xlsx';
import { jsPDF } from 'jspdf';
import 'jspdf-autotable';
import { EVFleetService } from 'app/shared/services/ev-space/ev-fleet.service';
import { DatePipe } from '@angular/common';

export class ModerateList {
  id: number;
  brand_id: number;
  brand_name: string;
  vehicle_type_id: string;
  vehicleType_name: string;
  connector_type_id: string;
  charger_type_id: string;
  name: string;
  driver_range: string;
  image_url: string;
  created_date: string;
  dateConvert:string;
  created_by: number;
  status: string;
  statusString: string;

}


@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations,
  providers:[DatePipe]
})

export class ListComponent implements OnInit {

  displayedColumns: string[] = ['brand_name', 'vehicleType_name', 'name','dateConvert', 'status', 'action'];
  head = [['Brand Name', 'Vehicle Type', 'Name','Created Date', 'Status']];
  dataSource: MatTableDataSource<ModerateList>;
  filterEntity: ModerateList;
  filterType: MatTableFilter;
  localStorage: any;
  moderateList: ModerateList[];
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
  dialogRef: any;
  brandSearch = false;
  vehicleSearch = false;
  nameSearch = false;
  capcitySearch = false;
  roadSearch = false;
  statusSearch = false;
  dateConvertSearch= false;
  exportedData: any[];

  constructor(private evFleetService:EVFleetService, public dialog: MatDialog,
    private snackBar: MatSnackBar,public datepipe: DatePipe) {

  }

  ngOnInit() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.getModrateList();
    this.filterEntity = new ModerateList();
    this.filterType = MatTableFilter.ANYWHERE;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  getModrateList() {
    this.evFleetService.getAllModerateVehicle().subscribe(
      (resp: any) => {
        if (resp != null) {
          this.moderateList = resp.data;
          for (var i = 0; i < this.moderateList.length; i++) {
            this.moderateList[i].dateConvert = this.datepipe.transform(this.moderateList[i].created_date, 'dd/MM/yyyy hh:mm:ss');
            if (this.moderateList[i].status == 'N') {
              this.moderateList[i].statusString = 'Inactive';
            } else {
              this.moderateList[i].statusString = 'Moderate';
            }
          }
          this.dataSource = new MatTableDataSource(this.moderateList);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        } else {
          this.snackBar.open(MessageConstant.NoRecordFoundMessage, 'Dismiss', { duration: 3000 })
          return;
        }
      }, (err) => {

      }
    )
  }

  delete(id) {
    this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
      disableClose: true
    });
    this.confirmDialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.evFleetService.deleteEvFleet(id, this.localStorage.userId).subscribe(
          (resp: any) => {
            if(resp.status == true){
              this.getModrateList();
              this.snackBar.open(MessageConstant.deleteMessage, 'Dismiss', { duration: 2000 });
            }
            
          }, (err) => {

          }
        )
      }
      this.confirmDialogRef = null;
    });

  }

  openDialog(action, data): void {
    let params = {
      action: action,
      data: data,

    }
    const dialogRef = this.dialog.open(ActionDialogComponent, {
      width: '800px',
      data: params,
      autoFocus: false,
      panelClass: 'app-action-dialog',
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      this.getModrateList();
    });
  }

  viewDialog(action, data, buttonAction): void {
    let params = {
      action: action,
      data: data,
      buttonAction: buttonAction
    }
    const dialogRef = this.dialog.open(ViewDialogComponent, {
      width: '800px',
      data: params,
      panelClass: 'app-action-dialog',
      autoFocus: false,
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      this.getModrateList();
    });
  }
  changeVisibility(visibleOption, ev) {
    ev.stopPropagation();
    if (visibleOption == 'brandSearch') {
      this.brandSearch = !this.brandSearch;
      this.filterEntity.brand_name = '';
    }
    if (visibleOption == 'vehicleSearch') {
      this.vehicleSearch = !this.vehicleSearch;
      this.filterEntity.vehicleType_name = '';
    }
    if (visibleOption == 'nameSearch') {
      this.nameSearch = !this.nameSearch;
      this.filterEntity.name = '';
    }
    if (visibleOption == 'statusSearch') {
      this.statusSearch = !this.statusSearch;
      this.filterEntity.statusString = '';
    }
    if(visibleOption == 'dateConvertSearch'){
      this.dateConvertSearch = !this.dateConvertSearch;
      this.filterEntity.dateConvert  = '';
    }
  }

  tableExport() {
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var moderateModel = this.dataSource.filteredData[i];

      var data = {
        'Brand Name': moderateModel.brand_name, 'Vechicle Type': moderateModel.vehicleType_name, 'Name': moderateModel.name,
        'Created Date':moderateModel.dateConvert,'Status': moderateModel.statusString
      };
      this.exportedData.push(data);
    }
    const workSheet = XLSX.utils.json_to_sheet(this.exportedData);
    const workBook: XLSX.WorkBook = XLSX.utils.book_new();

    XLSX.utils.book_append_sheet(workBook, workSheet, 'Ev Fleet Modrate List');
    XLSX.writeFile(workBook, 'Ev Fleet Modrate.xlsx');
  }

  pdfExport() {

    var doc = new jsPDF({

    });
    this.exportedData = [];
    for (let i = 0; i < this.dataSource.filteredData.length; i++) {
      var moderateModel = this.dataSource.filteredData[i];

      var data = [moderateModel.brand_name, moderateModel.vehicleType_name, moderateModel.name,moderateModel.dateConvert, moderateModel.statusString];

      this.exportedData.push(data);
    }
    var img = new Image();
    var imgRight = new Image();
    var src = 'assets/images/logos/logo_sparkev_main.png';
    var srcSparkEV = 'assets/images/logos/logo_SparkEV_black.png';
    imgRight.src = srcSparkEV;
    var createdDate = new Date().toLocaleString(('en-GB'));
    img.src = src;
    doc.setFontSize(11);
    doc.setTextColor(100);
    doc.setFontSize(18);

    (doc as any).autoTable({
      head: this.head,
      body: this.exportedData,
      theme: 'grid',
      headStyles: { fontStyle: 'majalla', fillColor: [11, 148, 68] },
      styles: {
        cellPadding: 1,
        fontSize: 8,

      },

      margin: { top: 22 },
      didDrawCell: data => {
      },
      didDrawPage: data => {
        doc.addImage(img , 15, 7, 15, 6);
        doc.addImage(imgRight, 170, 7, 25, 6);

        doc.text('Ev Fleet Modrate List', doc.internal.pageSize.getWidth() / 2, 11, { align: 'center' });

        doc.setTextColor(211, 211, 211);
        doc.setFontSize(60);
        doc.setGState(doc.GState({ opacity: 0.6 }));
        doc.text('Confidential/Not for Public use', 25, 285, null, 60,);
        doc.setGState(doc.GState({ opacity: 1 }));
        doc.setTextColor(100);
        doc.setFontSize(9);
        doc.text('Date:' + createdDate, 15, 18);

        doc.text('Generated By:' + this.localStorage.f_Name, 195, 18, { align: 'right' });
        doc = this.addWaterMark(doc);
      }
    })

    // below line for Open PDF document in new tab
    doc.output('dataurlnewwindow')

    // below line for Download PDF document  
    doc.save('Ev Fleet Modrate.pdf');

  }
  addWaterMark(doc) {
    var newpage = doc.getNumberOfPages();
    const i = doc.internal.getNumberOfPages();
    doc.setTextColor(100);
    doc.setFontSize(10);
    //Print Page 1 of 4 for example

    doc.text('Page ' + String(i) + ' of ' + String(newpage), 210 - 20, 297 - 10, null, null, "right")
    return doc;

  }
}

